import {Task} from '../types/task'
import {useHttp} from './http'
import {useQuery, useMutation, QueryKey} from 'react-query'
import {useEditConfig, useAddConfig, useDeleteConfig, useReorderTaskConfig} from './use-optimistic-options'
import { useDebounce } from "./index";
import {SortProps} from "./kanban"

export const useTasks = (param?: Partial<Task>) => {
	const client = useHttp();
	const debouncedParam = { ...param, name: useDebounce(param?.name, 200) };
	return useQuery<Task[]>(['tasks', debouncedParam], () => client('tasks', {data: debouncedParam}))
}

export const useAddTask = (queryKey: QueryKey) => {
	const client = useHttp();
	return useMutation(
		(params: Partial<Task>) => client(`tasks`, {
			method: 'POST',
			data: params
		}), 
		useAddConfig(queryKey)
	) 
}

export const useTask = (id?: number) => {
	const client = useHttp()
	return useQuery<Task>(
		['task', {id}],
		() => client(`tasks/${id}`),
		{
			enabled: Boolean(id)
		}
	)
}

export const useEditTask = (queryKey: QueryKey) => {
	const client = useHttp();
	return useMutation(
		(params: Partial<Task>) => client(`tasks/${params.id}`, {
			method: 'Patch',
			data: params
		}), 
		useEditConfig(queryKey)
	) 
}

export const useDeleteTask = (queryKey: QueryKey) => {
	const client = useHttp();
	return useMutation(
		({id}:{id: number}) => client(`tasks/${id}`, {
			method: 'DELETE',
		}), 
		useDeleteConfig(queryKey)
	) 
}


export const useReorderTask = (queryKey: QueryKey) => {
	const client = useHttp()
	return useMutation(
		(params: SortProps) => {
			return client('tasks/reorder', {
				data: params,
				method: 'POST'
			})
		},
		useReorderTaskConfig(queryKey)
	)
}