import { FormEvent } from 'react';
import { useAuth } from '../context/auth-context';
import {Form, Input, Button} from 'antd';
import { LongButton } from './index';
import {useAsync} from '../utils/use-async';

const apiUrl = process.env.REACT_APP_API_URL;

export const RegisterScreen = ({onError}:{onError:(error: Error) => void}) => {
	const {register, user} = useAuth()
	const {run, isLoading} = useAsync()

	const handleSubmit = async ({cpassword, ...values}: { username:string, password:string, cpassword:string }) => {
		if (cpassword != values.password) {
			onError(new Error('Passwords not match.'))
			return
		}
		try {
			await run(register(values));
		} catch(e: any) {
			onError(e)
		}
		
	}

	return <Form onFinish={handleSubmit}>
		<Form.Item name={'username'} rules={[{required: true, message: 'Please input username'}]}>
			<Input placeholder={'Username'} type="text" id={'username'}/>
		</Form.Item>
		<Form.Item name={'password'} rules={[{required: true, message: 'Please input password'}]}>
			<Input placeholder={'Password'} type="password" id={'password'}/>
		</Form.Item>
		<Form.Item name={'cpassword'} rules={[{required: true, message: 'Please confirm password'}]}>
			<Input placeholder={'Confirm Password'} type="password" id={'cpassword'}/>
		</Form.Item>
		<Form.Item>
			<LongButton loading={isLoading} htmlType={'submit'} type={"primary"}>Register</LongButton>
		</Form.Item>
			
	</Form>
}