import React from 'react'
import {Popover, Typography, List, Button, Divider} from 'antd'
import {useUsers} from '../utils/user'
import styled from '@emotion/styled'
import {ButtonNoPadding} from './lib'
import {useProjectModal} from '../screens/project-list/util'

export const UserPopover = () => {
	const {data: users, refetch} = useUsers()
	
	const content = <ContentContainer>
		<Typography.Text type={"secondary"}>Users List</Typography.Text>
		<List>
		{
			users?.map(user => <List.Item key={user.id}>
				<List.Item.Meta title={user.name} />
			</List.Item>)
		}
		</List>
		<Divider/>
	</ContentContainer>

	return (
		<Popover onVisibleChange={() => refetch()} placement={"bottom"} content={content}>
			<span>User</span>
		</Popover>
	)
}

const ContentContainer = styled.div`
min-width: 30rem;
`