import React, {useState} from 'react'
import {ScreenContainer, Row} from '../../components/lib'
import {useProjectInUrl, useKanbanSearchParams} from '../kanban/util'
import {useEpics, useDeleteEpic} from '../../utils/epic'
import {useTasks} from '../../utils/task'
import {List, Button, Modal} from 'antd'
import {Link} from 'react-router-dom'
import dayjs from 'dayjs'
import {useEpicsQueryKey, useEpicSearchParams} from './util'
import {CreateEpic} from './creat-epic'
import {Epic} from '../../types/epic'

export const EpicScreen = () => {
	const {data: currentProject} = useProjectInUrl()
	const {data: epics} = useEpics(useEpicSearchParams())
	const {data: tasks} = useTasks({projectId: currentProject?.id})
	const {mutate: deleteEpic } = useDeleteEpic(useEpicsQueryKey())
	const [epicCreateOpen, setEpicCreateOpen] = useState(false)

	const confirmDeleteEpic = (epic: Epic) => {
		Modal.confirm({
			title: `Confirm to delete Task: ${epic.name}`,
			content: 'Click to Delete',
			onOk() {
				deleteEpic({id: epic.id})
			}

		})
	}

	return <ScreenContainer>
		<Row between={true}>
			<h1>{currentProject?.name}Epic</h1>
			<Button onClick={() => setEpicCreateOpen(true)} type={'link'}>Create Task</Button>
		</Row>
		<List style={{overflow: 'scroll'}} dataSource={epics} itemLayout={'vertical'} renderItem={epic => <List.Item>
			<List.Item.Meta 
				title={<Row between={true}>
					<span>{epic.name}</span>
					<Button onClick={() => confirmDeleteEpic(epic)} type={'link'}>Delete</Button>
				</Row>}
				description={<div>
					<div>Start Time: {dayjs(epic.start).format('YYYY-MM-DD')}</div>
					<div>Finish Time: {dayjs(epic.end).format('YYYY-MM-DD')}</div>
				</div>} 
			/>
			<div>
				{tasks?.filter(task => task.epicId === epic.id).map(task => 
					<Link to={`/projects/${currentProject?.id}/kanban?editingTaskId=${task.id}`} key={task.id}>{task.name}</Link>)}
			</div>
		</List.Item>} />
		<CreateEpic onClose={() => setEpicCreateOpen(false)} visible={epicCreateOpen} />
	</ScreenContainer>
}