import React, {useEffect} from 'react'
import {Drawer, Form, Spin, Input, Button} from 'antd'
import {DrawerProps} from 'antd/es/drawer'
import styled from "@emotion/styled"
import {useAddEpic} from '../../utils/epic'
import {useEpicsQueryKey} from './util'
import {useForm} from 'antd/es/form/Form'
import {ErrorBox} from '../../components/lib'
import { UserSelect } from '../../components/user-select'
import {useProjectIdInUrl} from '../kanban/util'

export const CreateEpic = (props: Pick<DrawerProps, 'visible'> & {onClose: () => void}) => {
	const {mutate: addEpic, isLoading, error} = useAddEpic(useEpicsQueryKey())
	const [form] = useForm()
	const projectId = useProjectIdInUrl()
	
	const onFinish = async (values: any) => {
		await addEpic({...values, projectId})
		props.onClose()
	}

	useEffect(() => {
		form.resetFields()
	}, [form, props.visible])

	return <Drawer visible={props.visible} 
				   onClose={props.onClose} 
				   forceRender={true} 
				   destroyOnClose={true} 
				   width={'100%'}>
		<Container>
			{
				isLoading ? <Spin size={"large"} /> : (<>
					<h1>Create Tasks</h1>
					<ErrorBox error={error} />
					<Form form={form} layout={"vertical"} style={{width: '40rem'}} onFinish={onFinish}>
						<Form.Item label={'Name'} name={'name'} rules={[{required: true, message: 'Please input task name'}]}>
							<Input placeholder={'Please input task name'} />
						</Form.Item>

						<Form.Item style={{textAlign: "right"}}>
							<Button loading={isLoading} type={'primary'} htmlType={'submit'}>
								Submit
							</Button>
						</Form.Item>
					</Form>
				</>)
			}
		</Container>
	</Drawer>
}

const Container = styled.div`
height: 80vh;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
`