import React from 'react';
import {User} from '../../types/user'
import { Table, Dropdown, Menu, Modal } from 'antd';
import dayjs from 'dayjs';
import { TableProps } from 'antd/es/table';
import { Link } from 'react-router-dom'
import { Pin } from '../../components/pin'
import {useEditProject,useDeleteProject} from '../../utils/project'
import {ButtonNoPadding} from '../../components/lib'
import {useProjectModal,useProjectsQueryKey} from './util'
import {Project} from '../../types/project'


interface ListProps extends TableProps<Project>{
	users: User[];
}
export const List = ({ users , ...props }: ListProps) => {
	const {mutate} = useEditProject(useProjectsQueryKey())
	
	const pinProject = (id: number) => (pin: boolean) => mutate({id, pin})
	
	return <Table pagination={false} columns={[
		{
			title: <Pin checked={true} disabled={true}/>,
			render(value, project) {
				return <Pin checked={project.pin} onCheckedChange={pinProject(project.id)} />
			}
		},
		{
			title: 'Title',
			sorter: (a, b) => a.name.localeCompare(b.name),
			render(value, project) {
				return <Link to={`projects/${String(project.id)}`}>{project.name}</Link>
			}
		}, {
			title: 'Department',
			dataIndex: 'organization',
		}, {
			title: 'Person',
			render(value, project) {
				return <span>
					{users.find(user => user.id === project.personId)?.name || 'undefined'}
				</span>
			}
		}, {
			title: 'Created Time',
			render(value, project) {
				return <span>
					{project.created ? dayjs(project.created).format('YYYY-MM-DD') : 'None'}
				</span>
			}
		}, {
			render(value, project) {
				return <More project={project}/>
					
				
			}
		}
	]} {...props}/>
}

const More = ({project}: {project: Project}) => {
	const {startEdit} = useProjectModal()
	const editProject = (id: number) => () => startEdit(id)
	const {mutate: deleteProject} = useDeleteProject(useProjectsQueryKey())
	const confirmDeleteProject = (id: number) => {
		Modal.confirm({
			title: 'Do you want delete this project?',
			content: 'Click Yes to delete',
			okText: 'Yes',
			onOk() {
				deleteProject({id})
			}
		})
	}
	return (
		<Dropdown overlay={
			<Menu>
				<Menu.Item onClick={editProject(project.id)} key={'edit'}>Edit</Menu.Item>
				<Menu.Item onClick={() => confirmDeleteProject(project.id)} key={'delete'}>Delete</Menu.Item>
			</Menu>
		}>
			<ButtonNoPadding type={"link"}>...</ButtonNoPadding>
		</Dropdown>
	)
}